p {
  margin: 0;
}

.box {
  width: 600px;
  height: 600px;
  border: 10px solid #000;
  position: relative;
}

.game {
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.sub-box {
  cursor: pointer;
  width: 33.3333%;
  height: 33.3333%;
  cursor: pointer;
  box-sizing: border-box;
  border: 3px solid #000;
  justify-content: center;
  align-items: center;
  padding: 10px;
  font-size: 100px;
  display: flex;
}

.markerO {
  width: 80%;
  height: 80%;
  box-sizing: border-box;
  border: 25px solid red;
  border-radius: 50%;
}

.markerX {
  width: 100%;
  height: 100%;
  position: relative;
}

.stick1, .stick2 {
  height: 100%;
  width: 15%;
  background-color: #00f;
  border-radius: 15px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%)rotate(45deg);
}

.stick2 {
  transform: translate(-50%)rotate(-45deg);
}

.overlay {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(5px);
  background: rgba(255, 255, 255, .2);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  display: none;
}

.banner {
  width: 80%;
  height: 50%;
  box-sizing: border-box;
  background-color: #fdff92;
  border: 5px solid #000;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.message {
  margin: 0;
  font-size: 300%;
}

.restart-button {
  width: 40%;
  height: 30%;
  cursor: pointer;
  margin-top: 45px;
  font-size: 180%;
}

.winner {
  background-color: #55ef55;
}

